.rb-nose-wrapper,
.rb-colour-wrapper {
  display: flex;
  justify-content: space-around; }

h3 {
  font-size: 1.75rem;
  color: #373d51;
  padding: 1.3rem;
  margin: 0; }

.accordion a {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  padding: 1rem 3rem 1rem 1rem;
  color: #7288a2;
  font-size: 1.15rem;
  font-weight: 400;
  border-bottom: 1px solid #e5e5e5; }

.accordion a::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  content: "\f067";
  position: absolute;
  float: right;
  right: 1rem;
  font-size: 1rem;
  color: #7288a2;
  padding: 5px;
  width: 20px;
  height: 20px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 1px solid #7288a2;
  text-align: center; }

.accordion a:hover,
.accordion a:hover::after {
  cursor: pointer;
  color: #cad2d3; }

.accordion a:hover::after {
  border: 1px solid whitesmoke; }

.accordion a.active {
  color: whitesmoke;
  border-bottom: 1px solid whitesmoke; }

.accordion a.active::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  content: "\f068";
  color: whitesmoke;
  border: 1px solid whitesmoke; }

.accordion .info {
  opacity: 0;
  padding: 0 1rem;
  max-height: 0;
  border-bottom: 1px solid #e5e5e5;
  overflow: hidden;
  clear: both;
  -webkit-transition: all 0.2s ease 0.15s;
  -o-transition: all 0.2s ease 0.15s;
  transition: all 0.2s ease 0.15s; }

.accordion .info p {
  font-size: 1rem;
  font-weight: 300; }

.accordion .info.active {
  opacity: 1;
  padding: 1rem;
  max-height: 100%;
  -webkit-transition: all 0.35s ease 0.15s;
  -o-transition: all 0.35s ease 0.15s;
  transition: all 0.35s ease 0.15s; }

.rocket-panel {
  display: flex;
  align-items: center;
  justify-content: center; }

.rocket-wrapper {
  height: 75%;
  width: 50%;
  min-width: 200px; }

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .col {
    width: 100%; } }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

#app {
  min-width: 100vw;
  min-height: 100vh;
  background-color: lightseagreen;
  display: flex;
  flex-wrap: wrap; }

/* ---------------- 
GLOBAL 
---------------- */
.col {
  width: 50%; }

.b {
  border: 1px solid red; }
